<template>
    <div v-el:item class="vue-grid-layout" :style="mergedStyle">
        <slot></slot>
    </div>
    <!--<pre>{{width|json}}</pre>-->
</template>
<style>
    .vue-grid-layout {
        position: relative;
        transition: height 200ms ease;
    }
</style>
<script>
    var Vue = require('vue');

    var elementResizeDetectorMaker = require("element-resize-detector");

    import {bottom, compact, getLayoutItem, moveElement, validateLayout} from './utils';
    import GridItem from './GridItem.vue'

    export default {
        name: "GridLayout",
        components: {
            GridItem,
        },
        props: {
            autoSize: {
                type: Boolean,
                default: true
            },
            colNum: {
                type: Number,
                default: 12
            },
            rowHeight: {
                type: Number,
                default: 150
            },
            maxRows: {
                type: Number,
                default: Infinity
            },
            margin: {
                type: Array,
                default: function () { return [10, 10]; }
            },
            isDraggable: {
                type: Boolean,
                default: true
            },
            isResizable: {
                type: Boolean,
                default: true
            },
            useCssTransforms: {
                type: Boolean,
                default: true
            },
            verticalCompact: {
                type: Boolean,
                default: true
            },
            width: {
                type: Number,
                required: false,
                default: null
            },

            layout: [],
        },
        data: function() {
            return {
                mergedStyle: {},
                lastLayoutLength: 0
            };
        },
        ready() {
            validateLayout(this.layout);
            var self = this;
            window.onload = function() {
                if (self.width === null) {
                    self.onWindowResize();
                    //self.width = self.$el.offsetWidth;
                    window.addEventListener('resize', self.onWindowResize);
                }
                compact(self.layout, self.verticalCompact);

                self.updateHeight();
                self.$nextTick(function() {
//                    self.onWindowResize();
                    var erd = elementResizeDetectorMaker({
                        strategy: "scroll" //<- For ultra performance.
                    });
                    erd.listenTo(self.$els.item, function(element) {
                        self.onWindowResize();
                        /*var width = element.offsetWidth;
                         var height = element.offsetHeight;
                         console.log("Size: " + width + "x" + height);*/
                    });
                });

            }
        },
        watch: {
            width: function() {
                this.$nextTick(function() {
                    this.$broadcast("updateWidth", this.width);
                    this.updateHeight();
                });
            },
            layout: function() {
                if (this.layout !== undefined && this.layout.length !== this.lastLayoutLength) {
                    this.lastLayoutLength = this.layout.length;
                    compact(this.layout, this.verticalCompact);

                    //this.$nextTick(function () {
                        this.$broadcast("updateWidth", this.width);
                        this.updateHeight();
                    //});
                }
            }
        },
        methods: {
            updateHeight: function() {
                this.mergedStyle = {
                    height: this.containerHeight()
                };
            },
            onWindowResize: function() {
                /*if (this.$parent !== null && this.$parent.$el.offsetWidth !== undefined) {
                    this.width = this.$parent.$el.offsetWidth;
                }*/
                /*console.log("### WIDTH: " + this.$el.offsetWidth);
                console.log("### WIDTH 2: " + this.$els.item.offsetWidth);*/
                if (this.$els !== null && this.$els.item !== null) {
                    this.width = this.$els.item.offsetWidth;
                }
            },
            containerHeight: function() {
                if (!this.autoSize) return;
                return bottom(this.layout) * (this.rowHeight + this.margin[1]) + this.margin[1] + 'px';
            }
        },
        events: {
            dragEvent: function(eventName, id, x, y) {
                if (eventName === "drag" && x == 0 && y == 0) {
                    return;
                }
                //console.log(eventName + " id=" + id + ", x=" + x + ", y=" + y);
                var l = getLayoutItem(this.layout, id);

                /*
                 // Create placeholder (display only)
                 var placeholder = {
                     w: l.w, h: l.h, x: l.x, y: l.y, placeholder: true, id: id
                 };
                 */

                // Move the element to the dragged location.
                this.layout = moveElement(this.layout, l, x, y, true);
                compact(this.layout, this.verticalCompact);
                // needed because vue can't detect changes on array element properties
                this.$broadcast("compact", this.layout);
                this.updateHeight();
            },
            resizeEvent: function(eventName, id, h, w) {
                if (eventName === "drag" && h < -40 && w < -40) {
                    return;
                }
//                console.log(eventName + " id=" + id);

                /*
                 // Create placeholder (display only)
                 var placeholder = {
                     w: l.w, h: l.h, x: l.x, y: l.y, placeholder: true, id: id
                 };
                 */

                // Move the element to the dragged location.
                compact(this.layout, this.verticalCompact);
                this.$broadcast("compact", this.layout);
                this.updateHeight();
            },
        }
    }
</script>


